            Weiterbildungsprogrammen an.
          </p>
          <p>
            Bachelorstudien sind ordentliche Studien, die der wissenschaftlichen
            Berufsvorbildung und der Qualifizierung für berufliche Tätigkeiten
            dienen, welche die Anwendung wissenschaftlicher Erkenntnisse und
            Methoden erfordern. Bachelorstudien an der TU Wien umfassen 6
            Semester Regelstudienzeit und werden grundsätzlich in deutscher
            Sprache abgehalten. Ihr Umfang entspricht 180 ECTS. Die Studien
            schließen mit dem akademischen Titel Bachelor of Science (BSc) ab.
          </p>
          <p>
            Masterstudien sind ordentliche Studien, die der Vertiefung und
            Ergänzung der wissenschaftlichen Berufsvorbildung dienen. Die
            Masterstudien an der TU Wien umfassen 4 Semester Regelstudienzeit,
            ihr Umfang entspricht 120 ECTS. Es werden acht englischsprachige
            Masterstudien angeboten. Trotz der Bezeichnung Masterstudium
            schließen die Studien mit dem bekannten Titel Diplomingenieur
            (Dipl.-Ing.) ab. Der Bescheid über die Verleihung des akademischen
            Titels wird deutsch und englisch ausgestellt. In der englischen
            Version wird die Gleichwertigkeit mit dem Master of Science (MSc)
            bestätigt.
          </p>
          <p>
            Doktoratsstudien umfassen im Kern die eigene wissenschaftliche
            Forschungsleistung. Mit dem erarbeiteten neuen Wissen wird ein
            wichtiger Beitrag zur Entwicklung der Wissenschaft geleistet. Die
            Dissertation am Ende des Doktorats stellt die Zusammenfassung der
            Ergebnisse dieser Forschungsarbeit dar. Das Doktoratsstudium umfasst
            6 Semester Regelstudienzeit. Es besteht aus der Anfertigung der
            Dissertation im Rahmen von 162 ECTS und der Absolvierung von
            Lehrveranstaltungen im Umfang von insgesamt 18 ECTS.
          </p>
