import { createRouter, createWebHashHistory } from "vue-router";
import Home from "../views/Home.vue";

const routes = [
  // Home
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/osa-home",
    name: "OSA Home",
    component: () => import("../views/OSALandingPage.vue"),
  },
  
  // OSA Informatik
  {
    path: "/informatik",
    name: "Informatik",
    component: () => import("../views/inf/InformatikHome.vue"),
  },
  {
    path: "/informatik/einfuehrung",
    name: "InformatikIntroduction",
    component: () => import("../views/inf/InformatikIntroductionView.vue")
  },
  {
    path: "/informatik/sortieren-mit-system",
    name: "SortingView",
    component: () => import("../views/inf/SortingView.vue"),
  },
  {
    path: "/informatik/fit-i",
    name: "FIT-I",
    component: () => import("../views/inf/FIT-I.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach(() => {
  window.scrollTo(0,0)
});

export default router;
