import { createStore } from "vuex";

export default createStore({
  state: {
    showSubNavigation: false,
    subNavigationQuitRoute: '/',
    headerPageTitle: ''
  },
  mutations: {
    SET_SHOWSUBNAVIGATION(state, newValue){
      state.showSubNavigation = newValue;
    },
    SET_SUBNAVIGATIONQUITROUTE(state, newRoute){
      state.subNavigationQuitRoute = newRoute;
    },
    SET_HEADERPAGETITLE(state, newValue){
      state.headerPageTitle = newValue;
    }
  },
  actions: {
    setShowSubNavigation({commit, state}, newValue){
      commit('SET_SHOWSUBNAVIGATION', newValue);
      return state.showSubNavigation;
    },
    setSubNavigationQuitRoute({commit, state}, newRoute){
      commit('SET_SUBNAVIGATIONQUITROUTE', newRoute);
      return state.subNavigationQuitRoute;
    },
    setHeaderPageTitle({commit, state}, newValue){
      commit('SET_HEADERPAGETITLE', newValue);
      return state.headerPageTitle;
    }
  },
  modules: {},
  getters: {
    getShowSubNavigation: state => {return state.showSubNavigation},
    getSubNavigationQuitRoute: state => {return state.subNavigationQuitRoute},
    getHeaderPageTitle: state => {return state.headerPageTitle}
  }
});
